/**
 * nako_prepare.js
 * 
 * 
 */
class ReplaceHistory {
    constructor(from, to, index) {
        this.from = from;
        this.to = to;
        this.index = index;
    }
}
class ConvertResult {
    constructor(text, sourcePosition) {
        this.text = text;
        this.sourcePosition = sourcePosition;
    }
}
/**
 * 
 */
export class Replace {
    constructor(code) {
        this.history = [];
        this.code = code;
    }
    getText() {
        return this.code;
    }
    replaceAll(from, to) {
        while (true) {
            const index = this.getText().indexOf(from);
            if (index === -1) {
                break;
            }
            if (from.length !== to.length) {
                this.history.unshift(new ReplaceHistory(from.length, to.length, index));
            }
            this.code = this.code.replace(from, to);
        }
    }
    getSourcePosition(i) {
        // 1.40.15i
        for (const item of this.history) {
            if (i >= item.index + item.to) { // 
                i += item.from - item.to;
            }
            else if (item.index <= i && i < item.index + item.to) { // 
                // 2
                if (item.to >= 2 && i === item.index + item.to - 1) {
                    i = item.index + item.from - 1;
                }
                else {
                    i = item.index;
                }
            }
        }
        return i;
    }
}
/**
 * 
 *    [memo]
 *    
 *    for https://github.com/kujirahand/nadesiko3/issues/94
 */
export class NakoPrepare {
    constructor() {
        // 
        this.convertTable = new Map([
            // 
            // ) https://hydrocul.github.io/wiki/blog/2014/1101-hyphen-minus-wave-tilde
            // 0x2d: true, // ASCII
            [0x2010, '-'],
            [0x2011, '-'],
            [0x2013, '-'],
            [0x2014, '-'],
            [0x2015, '-'],
            [0x2212, '-'],
            // 
            // 0x7e: true,
            [0x02dc, '~'],
            [0x02F7, '~'],
            [0x2053, '~'],
            [0x223c, '~'],
            [0x301c, '~'],
            [0xFF5E, '~'],
            // 
            // ) http://anti.rosx.net/etc/memo/002_space.html
            // 0x20: true,
            [0x2000, ' '],
            [0x2002, ' '],
            [0x2003, ' '],
            [0x2004, ' '],
            [0x2005, ' '],
            [0x2006, ' '],
            [0x2007, ' '],
            [0x2009, ' '],
            [0x200A, ' '],
            [0x200B, ' '],
            [0x202F, ' '],
            [0x205F, ' '],
            [0x3000, ' '],
            [0x3164, ' '],
            // 
            // [0x09, ' '], // TAB --> SPC
            [0x203B, '#'],
            [0x3002, ';'],
            [0x3010, '['],
            [0x3011, ']'],
            // , (#877)
            [0x3001, ','],
            [0xFF0C, ','],
            [0x2716, '*'],
            [0x2795, '+'],
            [0x2796, '-'],
            [0x2797, ''] //  (#1183)
        ]);
    }
    /**  */
    static getInstance() {
        if (!NakoPrepare._instance) {
            NakoPrepare._instance = new NakoPrepare();
        }
        return NakoPrepare._instance;
    }
    // 
    /**
     * @param {string} ch
     */
    convert1ch(ch) {
        if (!ch) {
            return '';
        }
        const c = ch.codePointAt(0) || 0;
        // 
        const c2 = this.convertTable.get(c) || '';
        if (c2) {
            return c2;
        }
        // ASCII
        if (c < 0x7F) {
            return ch;
        }
        //  --- '' - ''
        if (c >= 0xFF01 && c <= 0xFF5E) {
            const c2 = c - 0xFEE0;
            return String.fromCodePoint(c2);
        }
        return ch;
    }
    /** convert code */
    convert(code) {
        if (!code) {
            return [];
        }
        const src = new Replace(code);
        // 
        src.replaceAll('\r\n', '\n');
        src.replaceAll('\r', '\n');
        let flagStr = false; // 
        let flagStr2 = false; // 
        let endOfStr = ''; // 
        const res = [];
        let left = 0; // 
        let str = ''; // 
        // 
        let i = 0;
        while (i < src.getText().length) {
            const c = src.getText().charAt(i);
            const ch2 = src.getText().substr(i, 2);
            // 
            if (flagStr) {
                if (c === endOfStr) {
                    flagStr = false;
                    res.push(new ConvertResult(str + endOfStr, src.getSourcePosition(left)));
                    i++;
                    left = i;
                    continue;
                }
                str += c;
                i++;
                continue;
            }
            // 
            if (flagStr2) {
                if (ch2 === endOfStr) {
                    flagStr2 = false;
                    if (endOfStr === '') {
                        endOfStr = '*/';
                    } // 
                    res.push(new ConvertResult(str + endOfStr, src.getSourcePosition(left)));
                    i += 2;
                    left = i;
                    continue;
                }
                str += c;
                i++;
                continue;
            }
            // 
            if (c === '') {
                res.push(new ConvertResult(c, src.getSourcePosition(left)));
                i++;
                left = i;
                flagStr = true;
                endOfStr = '';
                str = '';
                continue;
            }
            if (c === '') {
                res.push(new ConvertResult(c, src.getSourcePosition(left)));
                i++;
                left = i;
                flagStr = true;
                endOfStr = '';
                str = '';
                continue;
            }
            if (c === '') {
                res.push(new ConvertResult(c, src.getSourcePosition(left)));
                i++;
                left = i;
                flagStr = true;
                endOfStr = '';
                str = '';
                continue;
            }
            // JavaScriptUTF-16 charAt  2 --- #726
            if (ch2 === '' || ch2 === '') {
                res.push(new ConvertResult(ch2, src.getSourcePosition(left)));
                i += 2;
                left = i;
                flagStr2 = true;
                endOfStr = ch2;
                str = '';
                continue;
            }
            const c1 = this.convert1ch(c);
            if (c1 === '"' || c1 === '\'') {
                res.push(new ConvertResult(c1, src.getSourcePosition(left)));
                i++;
                left = i;
                flagStr = true;
                endOfStr = c;
                str = '';
                continue;
            }
            //  (#725)
            if (c1 === '#') {
                res.push(new ConvertResult(c1, src.getSourcePosition(left)));
                i++;
                left = i;
                flagStr = true; // 
                endOfStr = '\n';
                str = '';
                continue;
            }
            // 
            if (ch2 === '//' || ch2 === '') {
                res.push(new ConvertResult('//', src.getSourcePosition(left))); // '//'
                i += 2;
                left = i;
                flagStr = true;
                endOfStr = '\n';
                str = '';
                continue;
            }
            // 
            if (ch2 === '') {
                res.push(new ConvertResult('/*', src.getSourcePosition(left))); // 
                i += 2;
                left = i;
                flagStr2 = true;
                endOfStr = '';
                str = '';
                continue;
            }
            //  (#731)
            if (ch2 === '/*') {
                res.push(new ConvertResult(ch2, src.getSourcePosition(left)));
                i += 2;
                left = i;
                flagStr2 = true;
                endOfStr = '*/';
                str = '';
                continue;
            }
            // 
            res.push(new ConvertResult(c1, src.getSourcePosition(left)));
            i++;
            left = i;
        }
        if (flagStr || flagStr2) {
            res.push(new ConvertResult(str + endOfStr, src.getSourcePosition(left)));
        }
        return res;
    }
}
/** (!) */
export function checkNakoMode(code, modeNames) {
    // 256
    code = code.substring(0, 256);
    // 
    code = code.replace(/(|)/, '!');
    // 
    code = code.replace(/\/\*.*?\*\//g, '');
    // 
    const lines = code.split(/[;\n]/, 30);
    for (let line of lines) {
        line = line.replace(/^\s+/, '').replace(/\s+$/, ''); // trim
        if (modeNames.indexOf(line) >= 0) {
            return true;
        }
    }
    return false;
}
