import { NewEmptyToken } from './nako_types.mjs';
/**
 * 
 */
export class NakoParserBase {
    constructor(logger) {
        this.logger = logger;
        this.stackList = []; // 
        this.tokens = [];
        this.usedFuncs = new Set();
        /** @type {import('./nako3.mjs').Ast[]} */
        this.stack = [];
        this.index = 0;
        /** (accept)
         * @type {import('./nako3.mjs').Ast[]}
         */
        this.y = [];
        /**  @type {string} */
        this.modName = 'inline';
        this.namespaceStack = [];
        /**
         * 
         * @type {Array<string>}
         */
        this.modList = [];
        /**  */
        this.funclist = {};
        this.funcLevel = 0;
        this.usedAsyncFn = false; // asyncFn
        /**
         * 
         * @type {Object.<string,Object>}
         */
        this.localvars = { '': { type: 'var', value: '' } };
        /**  @type {string} */
        this.genMode = 'sync'; // #637
        /** (#1140) @type {int} */
        this.arrayIndexFrom = 0;
        /** (#1140) @type {boolean} */
        this.flagReverseArrayIndex = false;
        /** (#1140) @type {boolean} */
        this.flagCheckArrayInit = false;
        /** () */
        this.recentlyCalledFunc = [];
        this.init();
    }
    init() {
        this.funclist = {}; // 
        this.reset();
    }
    reset() {
        this.tokens = []; // 
        this.index = 0; // tokens[] 
        this.stack = []; //  ... pushStack() popStack() 
        this.y = []; // accept()
        this.genMode = 'sync'; // #637, #1056
    }
    setFuncList(funclist) {
        this.funclist = funclist;
    }
    /**
     * 
     * @param {string[]} josiList 
     */
    popStack(josiList = undefined) {
        if (!josiList) {
            const t = this.stack.pop();
            if (t) {
                return t;
            }
            return null;
        }
        // josiList 
        for (let i = this.stack.length - 1; i >= 0; i--) {
            const t = this.stack[i];
            if (josiList.length === 0 || josiList.indexOf(t.josi) >= 0) {
                this.stack.splice(i, 1); // remove stack
                this.logger.trace('POP :' + JSON.stringify(t));
                return t;
            }
        }
        // 
        return null;
    }
    /**
     * saveStack  loadStack 
     * 
     */
    saveStack() {
        this.stackList.push(this.stack);
        this.stack = [];
    }
    loadStack() {
        this.stack = this.stackList.pop();
    }
    /** 
     * @param {string} name
     * @returns {any}
     */
    findVar(name) {
        // 
        if (this.localvars[name]) {
            return {
                name,
                scope: 'local',
                info: this.localvars[name]
            };
        }
        // ?
        if (name.indexOf('__') >= 0) {
            if (this.funclist[name]) {
                return {
                    name,
                    scope: 'global',
                    info: this.funclist[name]
                };
            }
            else {
                return undefined;
            }
        }
        // 
        const gnameSelf = `${this.modName}__${name}`;
        if (this.funclist[gnameSelf]) {
            return {
                name: gnameSelf,
                scope: 'global',
                info: this.funclist[gnameSelf]
            };
        }
        // 
        for (const mod of this.modList) {
            const gname = `${mod}__${name}`;
            if (this.funclist[gname]) {
                return {
                    name: gname,
                    scope: 'global',
                    info: this.funclist[gname]
                };
            }
        }
        //  (funclist)
        if (this.funclist[name]) {
            return {
                name,
                scope: 'system',
                info: this.funclist[name]
            };
        }
        return undefined;
    }
    /**
     * 
     */
    pushStack(item) {
        this.logger.trace('PUSH:' + JSON.stringify(item));
        this.stack.push(item);
    }
    /**
     * 
     */
    isEOF() {
        return (this.index >= this.tokens.length);
    }
    getIndex() {
        return this.index;
    }
    /**
     * 
     */
    check(ttype) {
        return (this.tokens[this.index].type === ttype);
    }
    /**
     *  2
     * @param a [1, 2, ... ]
     */
    check2(a) {
        for (let i = 0; i < a.length; i++) {
            const idx = i + this.index;
            if (this.tokens.length <= idx) {
                return false;
            }
            if (a[i] === '*') {
                continue;
            } // ()
            const t = this.tokens[idx];
            if (a[i] instanceof Array) {
                if (a[i].indexOf(t.type) < 0) {
                    return false;
                }
                continue;
            }
            if (t.type !== a[i]) {
                return false;
            }
        }
        return true;
    }
    /**
     * 
     */
    checkTypes(a) {
        const type = this.tokens[this.index].type;
        return (a.indexOf(type) >= 0);
    }
    /**
     * check2
     *  false 
     */
    accept(types) {
        const y = [];
        const tmpIndex = this.index;
        const rollback = () => {
            this.index = tmpIndex;
            return false;
        };
        for (let i = 0; i < types.length; i++) {
            if (this.isEOF()) {
                return rollback();
            }
            const type = types[i];
            if (type == null) {
                return rollback();
            }
            if (typeof type === 'string') {
                const token = this.get();
                if (token && token.type !== type) {
                    return rollback();
                }
                y[i] = token;
                continue;
            }
            if (typeof type === 'function') {
                const f = type.bind(this);
                const r = f(y);
                if (r === null) {
                    return rollback();
                }
                y[i] = r;
                continue;
            }
            if (type instanceof Array) {
                if (!this.checkTypes(type)) {
                    return rollback();
                }
                y[i] = this.get();
                continue;
            }
            throw new Error('System Error : accept broken : ' + typeof type);
        }
        this.y = y;
        return true;
    }
    /**
     * 
     */
    get() {
        if (this.isEOF()) {
            return null;
        }
        return this.tokens[this.index++];
    }
    /**  */
    getCur() {
        if (this.isEOF()) {
            throw new Error('');
        }
        const t = this.tokens[this.index++];
        if (!t) {
            throw new Error('');
        }
        return t;
    }
    unget() {
        if (this.index > 0) {
            this.index--;
        }
    }
    /**  */
    peek(i = 0) {
        if (this.isEOF()) {
            return null;
        }
        return this.tokens[this.index + i];
    }
    /**  def  */
    peekDef(def = null) {
        if (this.isEOF()) {
            if (!def) {
                def = NewEmptyToken();
            }
            return def;
        }
        return this.tokens[this.index];
    }
    /**
     * 
     */
    peekSourceMap() {
        const token = this.peek();
        if (token === null) {
            return { startOffset: undefined, endOffset: undefined, file: undefined, line: 0, column: 0 };
        }
        return { startOffset: token.startOffset, endOffset: token.endOffset, file: token.file, line: token.line, column: token.column };
    }
    /**
     * depth: 
     * typeName: type
     * @param {{ depth: number, typeName?: string }} opts
     * @param {boolean} debugMode
     */
    nodeToStr(node, opts, debugMode) {
        const depth = opts.depth - 1;
        const typeName = (name) => (opts.typeName !== undefined) ? opts.typeName : name;
        const debug = debugMode ? (' debug: ' + JSON.stringify(node, null, 2)) : '';
        if (!node) {
            return '(NULL)';
        }
        switch (node.type) {
            case 'not':
                if (depth >= 0) {
                    const subNode = node.value;
                    return `${typeName('')}${this.nodeToStr(subNode, { depth }, debugMode)}not${debug}`;
                }
                else {
                    return `${typeName('')}not`;
                }
            case 'op': {
                const node2 = node;
                let operator = node2.operator || '';
                const table = { eq: '', not: '!', gt: '>', lt: '<', and: '', or: '' };
                if (operator in table) {
                    operator = table[operator];
                }
                if (depth >= 0) {
                    const left = this.nodeToStr(node2.left, { depth }, debugMode);
                    const right = this.nodeToStr(node2.right, { depth }, debugMode);
                    if (node2.operator === 'eq') {
                        return `${typeName('')}${left}${right}${debug}`;
                    }
                    return `${typeName('')}${left}${right}${operator}${debug}`;
                }
                else {
                    return `${typeName('')}${operator}${debug}`;
                }
            }
            case 'number':
                return `${typeName('')}${node.value}`;
            case 'string':
                return `${typeName('')}${node.value}${debug}`;
            case 'word':
                return `${typeName('')}${node.value}${debug}`;
            case 'func':
                return `${typeName('')}${node.name || node.value}${debug}`;
            case 'eol':
                return '';
            case 'eof':
                return '';
            default: {
                let name = node.name;
                if (name) {
                    name = node.value;
                }
                if (typeof name !== 'string') {
                    name = node.type;
                }
                return `${typeName('')}${name}${debug}`;
            }
        }
    }
}
