/**
 * DNCL
 */
// import { NakoIndentError } from './nako_errors.mjs'
import { NewEmptyToken } from './nako_types.mjs';
import { joinTokenLines, splitTokens } from './nako_indent_inline.mjs';
// DNCL
const DNCL_KEYWORDS = ['!DNCL', 'DNCL'];
// 
const DNCL_SIMPLES = {
    ':': ['eq', '='],
    ':': ['', ''],
    '{:{': ['[', '['],
    '}:}': [']', ']'],
    'word:': ['', ''],
    'word:': ['word', ''],
    'word:': ['word', '']
};
/**
 * DNCL
 */
export function convertDNCL(tokens) {
    if (!useDNCLmode(tokens)) {
        return tokens;
    }
    // 
    const lines = splitTokens(tokens, 'eol');
    for (let i = 0; i < lines.length; i++) {
        const line = lines[i];
        if (line.length <= 1) {
            continue;
        } // 
        //  | 
        for (let j = 0; j < line.length; j++) {
            if (line[j].type === '|') {
                line[j].type = 'range_comment';
                continue;
            }
            break;
        }
        // 
        const t = line[0];
        if (t.type === 'word' && t.value === '') {
            line.splice(0, line.length, NewEmptyToken('word', '', t.indent, t.line, t.file), NewEmptyToken('word', '', t.indent, t.line, t.file));
        }
        // ^\s*,?(.+)(|)/
        const fi = findTokens(line, ['word:', 'word:']);
        if (fi > 0) {
            replaceAtohantei(line, fi);
        }
        const fi2 = findTokens(line, ['word:', 'word:']);
        if (fi2 > 0) {
            replaceAtohantei(line, fi2);
        }
        // ()  ()
        const nai = findTokens(line, ['word:']);
        if (nai >= 1) {
            const tt = line[nai];
            if (tt.josi === '') {
                line[nai - 1].josi = '';
                line.splice(nai, 1);
            }
        }
        //  (255)  (255)
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            line[ni].value = '';
            line[ni].josi = '';
            line.splice(ni + 1, 1);
        }
        // ''  ''
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            // 
            // 
            // DNCL
            // 
            const t = line[ni];
            t.value = '';
            t.josi = '';
            line.splice(ni + 1, 1);
        }
        // ',': '',
        for (;;) {
            const ni = findTokens(line, ['word:', 'comma:,', 'word:']);
            if (ni < 0) {
                break;
            }
            const sou = line[ni + 2];
            if (sou.josi === '') {
                sou.type = '';
                sou.value = '';
                sou.josi = '';
                line.splice(ni, 3, sou);
                continue;
            }
            else if (sou.josi === '') {
                const nakumosi = line[ni + 3];
                if (nakumosi.value.substring(0, 4) === '') {
                    sou.type = '';
                    sou.value = '';
                    sou.josi = '';
                    line.splice(ni, 3, sou);
                    if (nakumosi.value.length > 4) {
                        const nakumosiTudukiStr = nakumosi.value.substring(4);
                        const nakumosiToken = NewEmptyToken('word', nakumosiTudukiStr, nakumosi.indent, nakumosi.line, nakumosi.file);
                        if (nakumosiTudukiStr.match(/^\d/)) {
                            nakumosiToken.type = 'number';
                        }
                        line.splice(ni + 2, 0, nakumosiToken);
                        nakumosi.value = nakumosi.value.substring(0, 4);
                    }
                    nakumosi.type = '';
                    nakumosi.value = '';
                    nakumosi.josi = '';
                    continue;
                }
            }
            break;
        }
        // I11001()|()|
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            const fu = line[ni];
            fu.type = 'word';
            fu.value = '';
            fu.josi = '';
            line.splice(ni, 2, fu);
        }
        // I11001()|()|
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            const fu = line[ni];
            fu.type = 'word';
            fu.value = '';
            fu.josi = '';
            line.splice(ni, 2, fu);
        }
        //   
        for (;;) {
            const ni = findTokens(line, ['word:']);
            if (ni < 0) {
                break;
            }
            const fu = line[ni];
            fu.type = '';
            fu.value = '';
            fu.josi = '';
        }
        // '0'
        // '0'
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni >= 1) {
                replaceAllElementV(line, ni);
            }
            else {
                break;
            }
        }
        // '0'
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni >= 1) {
                replaceAllElementV(line, ni);
            }
            else {
                break;
            }
        }
        // 
        let j = 0;
        while (j < line.length) {
            const t = line[j];
            // 
            if (t.type === 'word' && t.value.length >= 2) {
                const c = t.value.charAt(t.value.length - 1);
                if (c === '' || c === '') {
                    t.value = t.value.substring(0, t.value.length - 1);
                    t.josi = '';
                    line.splice(j + 1, 0, NewEmptyToken('word', c, t.indent, t.line, t.file));
                }
                j++;
                continue;
            }
            j++;
        }
    }
    // 
    for (let i = 0; i < tokens.length; i++) {
        const t = tokens[i];
        const a = DNCL_SIMPLES[t.type + ':' + t.value];
        if (a !== undefined) {
            t.type = a[0];
            t.value = a[1];
        }
    }
    tokens = joinTokenLines(lines);
    return tokens;
}
function replaceAllElementV(line, ni) {
    //
    // const ni = findTokens(line, ['word:', 'word:'])
    //
    const t = line[ni];
    line[ni - 1].josi = '';
    const eq = NewEmptyToken('eq', '=', t.indent, t.line, t.file);
    const begin = NewEmptyToken('[', '[', t.indent, t.line, t.file);
    const end = NewEmptyToken(']', ']', t.indent, t.line, t.file);
    end.josi = '';
    const val = line[ni + 2];
    val.josi = '';
    const times = NewEmptyToken('number', 100, t.indent, t.line, t.file);
    times.josi = '';
    const mul = NewEmptyToken('word', '', t.indent, t.line, t.file);
    line.splice(ni, 4, eq, begin, val, end, times, mul);
}
function replaceAtohantei(tokens, fi) {
    // `(${r[1]})`
    const wo = findTokens(tokens, ['word:']);
    if (wo >= 0) {
        tokens[wo].type = '';
        tokens[wo].value = '';
    }
    const ga = findTokens(tokens, ['word:']);
    if (ga >= 0) {
        tokens[ga].type = '';
        tokens[ga].value = '';
    }
    // :(fi) (fi+1)
    tokens[fi + 1].value = '';
}
function findTokens(tokens, findTypeValue) {
    const findA = findTypeValue.map(s => s.split(':'));
    for (let i = 0; i < tokens.length; i++) {
        let flag = true;
        for (let j = 0; j < findA.length; j++) {
            const f = findA[j];
            const idx = i + j;
            if (idx >= tokens.length) {
                return -1;
            }
            if (tokens[idx].type === f[0] && tokens[idx].value === f[1]) {
                continue;
            }
            else {
                flag = false;
                break;
            }
        }
        if (flag) {
            return i;
        }
    }
    return -1;
}
function useDNCLmode(tokens) {
    // 100
    for (let i = 0; i < tokens.length; i++) {
        if (i > 100) {
            break;
        }
        const t = tokens[i];
        if (t.type === 'line_comment' && DNCL_KEYWORDS.indexOf(t.value) >= 0) {
            t.type = 'DNCL';
            return true;
        }
    }
    return false;
}
export const NakoDncl = {
    convert: convertDNCL
};
