// @ts-nocheck
export default {
    // @_CHARTJS
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: true,
        fn: function (data, sys) {
            // Chart.js
            if (!window.Chart) {
                throw new Error('Chart.js');
            }
            // Canvas
            if (!sys.__canvas) {
                throw new Error('Canvas ');
            }
            // 
            if (data['']) {
                data.type = data[''];
            }
            if (data['']) {
                data.data = data[''];
            }
            if (data['']) {
                data.options = data[''];
            }
            if (sys.__chartjs) {
                sys.__chartjs.destroy();
            }
            // eslint-disable-next-line no-undef
            const chart = new Chart(sys.__canvas, data);
            sys.__chartjs = chart;
            return chart;
        }
    },
    '': { type: 'const', value: {} },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            data = sys.__exec('', ['line', data, sys]);
            const d = {
                type: 'line',
                data,
                options: sys.__v0['']
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            // 
            const gopt = Object.assign({}, sys.__v0[''], { 'indexAxis': 'x' });
            data = sys.__exec('', ['bar', data, sys]);
            const d = {
                type: 'bar',
                data,
                options: gopt
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            // 
            const gopt = Object.assign({}, sys.__v0[''], { 'indexAxis': 'y' });
            data = sys.__exec('', ['bar', data, sys]);
            const d = {
                type: 'bar',
                data,
                options: gopt
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            // 
            const gopt = Object.assign({}, sys.__v0[''], {
                'indexAxis': 'x',
                'scales': {
                    x: { stacked: true },
                    y: { stacked: true }
                }
            });
            data = sys.__exec('', ['bar', data, sys]);
            const d = {
                type: 'bar',
                data,
                options: gopt
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            // 
            const gopt = Object.assign({}, sys.__v0[''], {
                'indexAxis': 'y',
                'scales': {
                    x: { stacked: true },
                    y: { stacked: true }
                }
            });
            data = sys.__exec('', ['bar', data, sys]);
            const d = {
                type: 'bar',
                data,
                options: gopt
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            // 
            const gopt = Object.assign({}, sys.__v0[''], {});
            data = sys.__exec('', ['scatter', data, sys]);
            const d = {
                type: 'scatter',
                data,
                options: gopt
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            data = sys.__exec('', ['pie', data, sys]);
            const d = {
                type: 'pie',
                data,
                options: sys.__v0['']
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            data = sys.__exec('', ['pie', data, sys]);
            const d = {
                type: 'doughnut',
                data,
                options: sys.__v0['']
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            data = sys.__exec('', ['pie', data, sys]);
            const d = {
                type: 'polarArea',
                data,
                options: sys.__v0['']
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [['', '', '']],
        pure: false,
        fn: function (data, sys) {
            data = sys.__exec('', ['bar', data, sys]);
            const d = {
                type: 'radar',
                data,
                options: sys.__v0['']
            };
            return sys.__exec('', [d, sys]);
        }
    },
    '': {
        type: 'func',
        josi: [[''], ['']],
        pure: false,
        fn: function (t, dataOrg, sys) {
            //  (#1416)
            const data = JSON.parse(JSON.stringify(dataOrg));
            const bgcolorList = [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(255, 159, 64, 0.2)'
            ];
            const fgcolorList = [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(255, 159, 64, 1)'
            ];
            const res = {};
            const bgcolors = [];
            const fgcolors = [];
            res.labels = [];
            // 
            if (data instanceof Array) {
                // 
                if (data[0] instanceof Array) {
                    if (t === 'pie') { // 
                        const o = {};
                        o.data = [];
                        res.datasets = [o];
                        for (let i = 0; i < data.length; i++) {
                            res.labels.push(data[i][0]); // label
                            o.data.push(data[i][1]); // value
                            bgcolors.push(bgcolorList[i % 6]);
                            fgcolors.push(fgcolorList[i % 6]);
                        }
                        o.backgroundColor = bgcolors;
                        o.borderColor = fgcolors;
                        return res;
                    }
                    // 
                    // [1,0]
                    if (typeof (data[1][0]) === 'string') {
                        for (let i = 1; i < data.length; i++) {
                            res.labels.push(data[i][0]); // 
                            data[i] = data[i].slice(1); // 
                        }
                        data[0] = data[0].slice(1); // 
                    }
                    else {
                        //  - 
                        for (let i = 1; i < data.length; i++) {
                            res.labels.push(i);
                        }
                    }
                    res.datasets = [];
                    for (let i = 0; i < data[0].length; i++) {
                        const o = {};
                        res.datasets.push(o);
                        o.label = data[0][i];
                        o.backgroundColor = bgcolorList[i % 6];
                        o.borderColor = fgcolorList[i % 6];
                        o.data = [];
                        for (let j = 1; j < data.length; j++) {
                            o.data.push(data[j][i]);
                        }
                    }
                    return res;
                }
                // 
                // 
                for (let i = 0; i < data.length; i++) {
                    res.labels.push(i + 1);
                    bgcolors.push(bgcolorList[i % 6]);
                    fgcolors.push(fgcolorList[i % 6]);
                }
                const o1 = {};
                res.datasets = [o1];
                o1.label = '';
                o1.data = data;
                o1.backgroundColor = bgcolors;
                o1.borderColor = fgcolors;
                return res;
            }
            if (data instanceof Object) {
                return data;
            }
            // 1
            return sys.__exec('', [[data], sys]);
        }
    }
};
