// @ts-nocheck
import hotkeys from 'hotkeys-js';
export default {
    // @
    '': {
        type: 'func',
        josi: [['', ''], ['']],
        pure: true,
        fn: function (key, fname, sys) {
            hotkeys(key, function (event, handler) {
                event.preventDefault();
                const f = sys.__findFunc(fname);
                f(sys);
            });
        }
    },
    '': {
        type: 'func',
        josi: [['', '']],
        pure: true,
        fn: function (key) {
            hotkeys.unbind(key);
        }
    }
};
