// 
// 
import { opPriority } from './nako_parser_const.mjs';
// 
// (memo) _replaceWord  word 
/** @types {Record<string, string>} */
import reservedWords from './nako_reserved_words.mjs';
import { isIndentChars } from './nako_indent_chars.mjs';
// 
import { josiRE, removeJosiMap, tararebaMap } from './nako_josi_list.mjs';
// 
import { rules, unitRE } from './nako_lex_rules.mjs';
import { NakoLexerError, InternalLexerError } from './nako_errors.mjs';
export class NakoLexer {
    /**
     * @param logger
     */
    constructor(logger) {
        this.logger = logger; // ,
        this.funclist = {};
        this.modList = []; // , --- nako3::lex 
        this.result = [];
        this.modName = 'main.nako3'; // 
    }
    /**  */
    setFuncList(listObj) {
        this.funclist = listObj;
    }
    /**
     * @param tokens
     * @param {boolean} isFirst
     * @param {string} filename
     */
    replaceTokens(tokens, isFirst, filename) {
        this.result = tokens;
        this.modName = NakoLexer.filenameToModName(filename);
        //  funclist 
        NakoLexer.preDefineFunc(tokens, this.logger, this.funclist);
        this._replaceWord(this.result);
        if (isFirst) {
            if (this.result.length > 0) {
                const eof = this.result[this.result.length - 1];
                this.result.push({
                    type: 'eol',
                    line: eof.line,
                    column: 0,
                    indent: -1,
                    file: eof.file,
                    josi: '',
                    value: '---',
                    startOffset: eof.startOffset,
                    endOffset: eof.endOffset,
                    rawJosi: ''
                }); // 
                this.result.push({
                    type: 'eof',
                    line: eof.line,
                    indent: -1,
                    column: 0,
                    file: eof.file,
                    josi: '',
                    value: '',
                    startOffset: eof.startOffset,
                    endOffset: eof.endOffset,
                    rawJosi: ''
                }); // 
            }
            else {
                this.result.push({
                    type: 'eol',
                    line: 0,
                    column: 0,
                    indent: -1,
                    file: '',
                    josi: '',
                    value: '---',
                    startOffset: 0,
                    endOffset: 0,
                    rawJosi: ''
                }); // 
                this.result.push({
                    type: 'eof',
                    line: 0,
                    column: 0,
                    indent: -1,
                    file: '',
                    josi: '',
                    value: '',
                    startOffset: 0,
                    endOffset: 0,
                    rawJosi: ''
                }); // 
            }
        }
        return this.result;
    }
    /**
     * funclist
     * static
     */
    static preDefineFunc(tokens, logger, funclist) {
        // 
        let i = 0;
        let isFuncPointer = false;
        const readArgs = () => {
            const args = [];
            const keys = {};
            if (tokens[i].type !== '(') {
                return [];
            }
            i++;
            while (tokens[i]) {
                const t = tokens[i];
                i++;
                if (t.type === ')') {
                    break;
                }
                if (t.type === 'func') {
                    isFuncPointer = true;
                }
                else if (t.type !== '|' && t.type !== 'comma') {
                    if (isFuncPointer) {
                        t.funcPointer = true;
                        isFuncPointer = false;
                    }
                    args.push(t);
                    if (!keys[t.value]) {
                        keys[t.value] = [];
                    }
                    keys[t.value].push(t.josi);
                }
            }
            const varnames = [];
            const funcPointers = [];
            const result = [];
            const already = {};
            for (const arg of args) {
                if (!already[arg.value]) {
                    const josi = keys[arg.value];
                    result.push(josi);
                    varnames.push(arg.value);
                    if (arg.funcPointer) {
                        funcPointers.push(arg.value);
                    }
                    else {
                        funcPointers.push(null);
                    }
                    already[arg.value] = true;
                }
            }
            return [result, varnames, funcPointers];
        };
        // 
        while (i < tokens.length) {
            // 
            const t = tokens[i];
            // xx** ... 
            if ((t.type === 'word' && t.josi === '') || (t.type === 'word' && t.josi === '~')) {
                t.josi = '';
                tokens.splice(i + 1, 0, { type: 'def_func', value: '', indent: t.indent, line: t.line, column: t.column, file: t.file, josi: '', startOffset: t.endOffset, endOffset: t.endOffset, rawJosi: '', tag: '' });
                i++;
                continue;
            }
            // NN|
            if (t.type === 'word' && t.josi === '' && t.value.length >= 2) {
                if (t.value.match(/$/)) {
                    t.value = t.value.substring(0, t.value.length - 1);
                    // N
                    if (!t.endOffset) {
                        t.endOffset = 1;
                    }
                    const kai = { type: '', value: '', indent: t.indent, line: t.line, column: t.column, file: t.file, josi: '', startOffset: t.endOffset - 1, endOffset: t.endOffset, rawJosi: '' };
                    tokens.splice(i + 1, 0, kai);
                    t.endOffset--;
                    i++;
                }
            }
            // 
            if (t.type === 'word' && reservedWords[t.value]) {
                t.type = reservedWords[t.value];
                if (t.value === '') {
                    t.value = '';
                }
            }
            // 
            if (t.type !== 'def_test' && t.type !== 'def_func') {
                i++;
                continue;
            }
            //  (1)
            let isMumei = true;
            let prevToken = { type: 'eol' };
            if (i >= 1) {
                prevToken = tokens[i - 1];
            }
            if (prevToken.type === 'eol') {
                isMumei = false;
            }
            // 
            const defToken = t;
            i++; // skip "" or ""
            let josi = [];
            let varnames = [];
            let funcPointers = [];
            let funcName = '';
            let funcNameToken = null;
            // 
            if (tokens[i] && tokens[i].type === '(') {
                [josi, varnames, funcPointers] = readArgs();
            }
            // 
            if (!isMumei && tokens[i] && tokens[i].type === 'word') {
                funcNameToken = tokens[i++];
                funcName = funcNameToken.value;
            }
            // 
            if (josi.length === 0 && tokens[i] && tokens[i].type === '(') {
                [josi, varnames, funcPointers] = readArgs();
            }
            // 
            // 
            if (funcName !== '' && funcNameToken) {
                const modName = NakoLexer.filenameToModName(t.file);
                funcName = modName + '__' + funcName;
                if (funcName in funclist) { // 
                    // main__ #1223
                    const dispName = funcName.replace(/^main__/, '');
                    logger.warn(`${dispName}`, defToken);
                }
                funcNameToken.value = funcName;
                funclist[funcName] = {
                    type: 'func',
                    josi,
                    fn: null,
                    asyncFn: false,
                    varnames,
                    funcPointers
                };
            }
            // 
            defToken.meta = {
                type: 'func',
                josi,
                varnames,
                funcPointers
            };
        }
    }
    /** {}null */
    splitStringEx(code) {
        /** @type {string[]} */
        const list = [];
        // "A{B}C{D}E" -> ["A", "B}C", "D}E"] -> ["A", "B", "C", "D", "E"]
        // "A{B}C}D{E}F" -> ["A", "B}C}D", "E}F"] -> ["A", "B", "C}D", "E", "F"]
        const arr = code.split(/[{]/);
        list.push(arr[0]);
        for (const s of arr.slice(1)) {
            const end = s.replace('', '}').indexOf('}');
            if (end === -1) {
                return null;
            }
            list.push(s.slice(0, end), s.slice(end + 1));
        }
        return list;
    }
    _replaceWord(tokens) {
        let comment = [];
        let i = 0;
        const getLastType = () => {
            if (i <= 0) {
                return 'eol';
            }
            return tokens[i - 1].type;
        };
        let modSelf = (tokens.length > 0) ? NakoLexer.filenameToModName(tokens[0].file) : 'main.nako3';
        while (i < tokens.length) {
            const t = tokens[i];
            // 
            if ((t.type === 'word' || t.type === 'func') && t.value === '') {
                modSelf = tokens[i - 1].value;
            }
            // ( word => func )
            if (t.type === 'word' && t.value !== '') {
                // 
                const funcName = t.value;
                if (funcName.indexOf('__') < 0) {
                    // 
                    const gname1 = `${modSelf}__${funcName}`;
                    const gfo1 = this.funclist[gname1];
                    if (gfo1 && gfo1.type === 'func') {
                        t.type = 'func';
                        t.meta = gfo1;
                        t.value = gname1;
                        continue;
                    }
                    // 
                    for (const mod of this.modList) {
                        const gname = `${mod}__${funcName}`;
                        const gfo = this.funclist[gname];
                        if (gfo && gfo.type === 'func') {
                            t.type = 'func';
                            t.meta = gfo;
                            t.value = gname;
                            break;
                        }
                    }
                    if (t.type === 'func') {
                        continue;
                    }
                }
                const fo = this.funclist[funcName];
                if (fo && fo.type === 'func') {
                    t.type = 'func';
                    t.meta = fo;
                }
            }
            // 
            // (ng) 5 - 3 || word - 3
            // (ok) ()-3 || 1 * -3 || A -3 
            if (t.type === '-' && tokens[i + 1] && tokens[i + 1].type === 'number') {
                // (||) 
                const ltype = getLastType();
                if (ltype === 'eol' || opPriority[ltype] || tokens[i - 1].josi !== '') {
                    tokens.splice(i, 1); // remove '-'
                    tokens[i].value *= -1;
                }
            }
            //  = 
            if (t.josi === undefined) {
                t.josi = '';
            }
            if (t.josi === '') {
                if (!t.rawJosi) {
                    t.rawJosi = t.josi;
                }
                const startOffset = (t.endOffset === undefined) ? undefined : t.endOffset - t.rawJosi.length;
                tokens.splice(i + 1, 0, {
                    type: 'eq',
                    indent: t.indent,
                    line: t.line,
                    column: t.column,
                    file: t.file,
                    startOffset,
                    endOffset: t.endOffset,
                    josi: '',
                    rawJosi: '',
                    value: undefined
                });
                i += 2;
                t.josi = t.rawJosi = '';
                t.endOffset = startOffset;
                continue;
            }
            // 
            if (t.josi === '') {
                if (!t.rawJosi) {
                    t.rawJosi = t.josi;
                }
                const startOffset = t.endOffset === undefined ? undefined : t.endOffset - t.rawJosi.length;
                tokens.splice(i + 1, 0, {
                    type: t.josi,
                    indent: t.indent,
                    line: t.line,
                    column: t.column,
                    file: t.file,
                    startOffset,
                    endOffset: t.endOffset,
                    josi: '',
                    rawJosi: '',
                    value: undefined
                });
                t.josi = t.rawJosi = '';
                t.endOffset = startOffset;
                i += 2;
                continue;
            }
            // 
            if (tararebaMap[t.josi]) {
                const josi = (t.josi === '' || t.josi === '') ? '' : '';
                if (!t.rawJosi) {
                    t.rawJosi = josi;
                }
                const startOffset = t.endOffset === undefined ? undefined : t.endOffset - t.rawJosi.length;
                tokens.splice(i + 1, 0, {
                    type: '',
                    value: josi,
                    indent: t.indent,
                    line: t.line,
                    column: t.column,
                    file: t.file,
                    startOffset,
                    endOffset: t.endOffset,
                    josi: '',
                    rawJosi: ''
                });
                t.josi = t.rawJosi = '';
                t.endOffset = startOffset;
                i += 2;
                continue;
            }
            // '_' +   ()
            if (t.type === '_eol') {
                tokens.splice(i, 1);
                continue;
            }
            // 
            if (t.type === 'line_comment' || t.type === 'range_comment') {
                comment.push(t.value);
                tokens.splice(i, 1);
                continue;
            }
            // 
            if (t.type === 'eol') {
                t.value = comment.join('/');
                comment = [];
            }
            i++;
        }
    }
    /**
     * 
     * @returns [, ]
     */
    countIndent(src) {
        let indent = 0;
        for (let i = 0; i < src.length; i++) {
            const c = src.charAt(i);
            const n = isIndentChars(c);
            if (n === 0) {
                return [indent, i];
            }
            indent += n;
        }
        return [indent, src.length];
    }
    /**
     * 
     * @param src 
     * @param line 
     * @param filename 
     */
    tokenize(src, line, filename) {
        const srcLength = src.length;
        const result = [];
        let columnCurrent;
        let lineCurrent;
        let column = 1;
        let isDefTest = false;
        let indent = 0;
        // 
        const ia = this.countIndent(src);
        indent = ia[0]; // 
        src = src.substring(ia[1]); // 
        column += ia[1];
        while (src !== '') {
            let ok = false;
            // 
            for (const rule of rules) {
                // 
                const m = rule.pattern.exec(src);
                if (!m) {
                    continue;
                }
                ok = true;
                // 
                if (rule.name === 'space') {
                    column += m[0].length;
                    src = src.substring(m[0].length);
                    continue;
                }
                // 
                if (rule.cbParser) {
                    // 
                    let rp;
                    if (isDefTest && rule.name === 'word') {
                        rp = rule.cbParser(src, false);
                    }
                    else {
                        try {
                            rp = rule.cbParser(src);
                        }
                        catch (e) {
                            throw new NakoLexerError(e.message, srcLength - src.length, srcLength - src.length + 1, line, filename);
                        }
                    }
                    if (rule.name === 'string_ex') {
                        //   aaa{x}bbb{x}cccc
                        const list = this.splitStringEx(rp.res);
                        if (list === null) {
                            throw new InternalLexerError('{...}', srcLength - src.length, srcLength - rp.src.length, line, filename);
                        }
                        let offset = 0;
                        for (let i = 0; i < list.length; i++) {
                            const josi = (i === list.length - 1) ? rp.josi : '';
                            if (i % 2 === 0) {
                                result.push({
                                    type: 'string',
                                    value: list[i],
                                    file: filename,
                                    josi,
                                    indent,
                                    line,
                                    column,
                                    preprocessedCodeOffset: srcLength - src.length + offset,
                                    preprocessedCodeLength: list[i].length + 2 + josi.length
                                });
                                // '"...{''}...{'
                                offset += list[i].length + 2;
                            }
                            else {
                                result.push({ type: '&', value: '&', josi: '', indent, file: filename, line, column, preprocessedCodeOffset: srcLength - src.length + offset, preprocessedCodeLength: 0 });
                                result.push({ type: '(', value: '(', josi: '', indent, file: filename, line, column, preprocessedCodeOffset: srcLength - src.length + offset, preprocessedCodeLength: 0 });
                                result.push({ type: 'code', value: list[i], josi: '', indent, file: filename, line, column, preprocessedCodeOffset: srcLength - src.length + offset, preprocessedCodeLength: list[i].length });
                                result.push({ type: ')', value: ')', josi: '', indent, file: filename, line, column, preprocessedCodeOffset: srcLength - src.length + offset + list[i].length, preprocessedCodeLength: 0 });
                                result.push({ type: '&', value: '&', josi: '', indent, file: filename, line, column, preprocessedCodeOffset: srcLength - src.length + offset + list[i].length, preprocessedCodeLength: 0 });
                                offset += list[i].length;
                            }
                        }
                        line += rp.numEOL;
                        column += src.length - rp.src.length;
                        src = rp.src;
                        if (rp.numEOL > 0) {
                            column = 1;
                        }
                        break;
                    }
                    columnCurrent = column;
                    column += src.length - rp.src.length;
                    result.push({ type: rule.name, value: rp.res, josi: rp.josi, indent, line, column: columnCurrent, file: filename, preprocessedCodeOffset: srcLength - src.length, preprocessedCodeLength: src.length - rp.src.length });
                    src = rp.src;
                    line += rp.numEOL;
                    if (rp.numEOL > 0) {
                        column = 1;
                    }
                    break;
                }
                // 
                const srcOffset = srcLength - src.length;
                // 
                let value = m[0];
                if (rule.cb) {
                    value = rule.cb(value);
                }
                // 
                columnCurrent = column;
                lineCurrent = line;
                column += m[0].length;
                src = src.substring(m[0].length);
                // 
                if ((rule.name === 'eol' && value === '\n') || rule.name === '_eol') {
                    value = line++;
                    column = 1;
                }
                //  --- #994
                if (rule.name === 'number') {
                    // 
                    const um = unitRE.exec(src);
                    if (um) {
                        src = src.substring(um[0].length);
                        column += m[0].length;
                    }
                }
                let josi = '';
                if (rule.readJosi) {
                    const j = josiRE.exec(src);
                    if (j) {
                        josi = j[0].replace(/^\s+/, '');
                        column += j[0].length;
                        src = src.substring(j[0].length);
                        //  #877
                        if (src.charAt(0) === ',') {
                            src = src.substring(1);
                        }
                        //  #939 #974
                        if (removeJosiMap[josi]) {
                            josi = '';
                        }
                    }
                }
                switch (rule.name) {
                    case 'def_test': {
                        isDefTest = true;
                        break;
                    }
                    case 'eol': { // eol
                        isDefTest = false;
                        break;
                    }
                    default: {
                        break;
                    }
                }
                // (#682) 
                if (rule.name === 'dec_lineno') {
                    line--;
                    continue;
                }
                result.push({
                    type: rule.name,
                    value,
                    indent,
                    line: lineCurrent,
                    column: columnCurrent,
                    file: filename,
                    josi,
                    preprocessedCodeOffset: srcOffset,
                    preprocessedCodeLength: (srcLength - src.length) - srcOffset
                });
                // column12(core #66)
                if (rule.name === 'eol' && column === 1) {
                    const ia = this.countIndent(src);
                    indent = ia[0];
                    column += ia[1];
                    src = src.substring(ia[1]); // 
                }
                break;
            }
            if (!ok) {
                throw new InternalLexerError(': ' + src.substring(0, 3) + '...', srcLength - src.length, srcLength - srcLength + 3, line, filename);
            }
        }
        return result;
    }
    // type
    static tokensToTypeStr(tokens, sep) {
        const a = tokens.map((v) => {
            return v.type;
        });
        return a.join(sep);
    }
    /**
     * 
     * @param {string} filename
     * @returns {string}
     */
    static filenameToModName(filename) {
        if (!filename) {
            return 'main';
        }
        // 
        filename = filename.replace(/[\\:]/g, '/'); // Windowspath/
        if (filename.indexOf('/') >= 0) {
            const a = filename.split('/');
            filename = a[a.length - 1];
        }
        filename = filename.replace(/\.nako3?$/, '');
        return filename;
    }
}
