/* eslint-disable quote-props */
export default {
    // @
    '': {
        type: 'func',
        josi: [['', '']],
        pure: true,
        fn: function (url, sys) {
            const a = new Audio();
            a.src = url;
            return a;
        },
        return_none: false
    },
    '': {
        type: 'func',
        josi: [['']],
        pure: true,
        fn: function (obj, sys) {
            if (!obj) {
                throw new Error('');
            }
            obj.loop = false;
            obj.play();
        },
        return_none: true
    },
    '': {
        type: 'func',
        josi: [['']],
        pure: true,
        fn: function (obj, sys) {
            if (!obj) {
                throw new Error('');
            }
            obj.loop = true;
            obj.play();
        },
        return_none: true
    },
    '': {
        type: 'func',
        josi: [['']],
        pure: true,
        fn: function (obj, sys) {
            if (!obj) {
                throw new Error('');
            }
            obj.pause();
            obj.currentTime = 0; // 
            // 0(#715)
            setTimeout(() => {
                obj.currentTime = 0; // 
            }, 10);
        },
        return_none: true
    },
    '': {
        type: 'func',
        josi: [['']],
        pure: true,
        fn: function (obj, sys) {
            if (!obj) {
                throw new Error('');
            }
            obj.pause();
        },
        return_none: true
    },
    '': {
        type: 'func',
        josi: [['', '']],
        pure: true,
        fn: function (obj, sys) {
            if (!obj) {
                throw new Error('');
            }
            return obj.volume;
        }
    },
    '': {
        type: 'func',
        josi: [[''], ['', '']],
        pure: true,
        fn: function (obj, v, sys) {
            if (!obj) {
                throw new Error('');
            }
            obj.volume = v;
        },
        return_none: true
    },
    '': {
        type: 'func',
        josi: [['', '']],
        pure: true,
        fn: function (obj, sys) {
            if (!obj) {
                throw new Error('');
            }
            return obj.duration;
        }
    },
    '': {
        type: 'func',
        josi: [['', '']],
        pure: true,
        fn: function (obj, sys) {
            if (!obj) {
                throw new Error('');
            }
            return obj.currentTime;
        }
    },
    '': {
        type: 'func',
        josi: [[''], ['', '']],
        pure: true,
        fn: function (obj, v, sys) {
            if (!obj) {
                throw new Error('');
            }
            obj.currentTime = v;
        },
        return_none: true
    }
};
