/**
 * 3
 */
export function isIndentChars(ch) {
    const code = ch.charCodeAt(0);
    // 
    if (ch === '\t') {
        return 4;
    }
    if (ch === ' ' || ch === '|') {
        return 1;
    }
    if (ch === '' || ch === '') {
        return 2;
    }
    if (ch === '' || ch === '') {
        return 2;
    } // 0x23CB, 0x23CC
    //  --- https://github.com/kujirahand/unicode-sheets/blob/main/box-drawing.md
    if (code >= 0x2500 && code <= 0x257F) {
        return 2;
    }
    //  --- https://github.com/kujirahand/unicode-sheets/blob/main/misc-technical.md
    if ((code >= 0x23A0 && code <= 0x23AF) ||
        (code >= 0x23B8 && code <= 0x23BF)) {
        return 2;
    }
    return 0;
}
