/**
 * DNCL ver2 
 */
// import { NakoIndentError } from './nako_errors.mjs'
import { NewEmptyToken } from './nako_types.mjs';
import { joinTokenLines, splitTokens } from './nako_indent_inline.mjs';
import { newToken, debugTokens } from './nako_tools.mjs';
const IS_DEBUG = false;
const DNCL_ARRAY_INIT_COUNT = 30;
// DNCL2
const DNCL2_KEYWORDS = ['!DNCL2', 'DNCL2', '!DNCL2', 'DNCL2'];
// 
const DNCL_SIMPLES = {
    ':': ['eq', '='],
    ':': ['', ''],
    '{:{': ['[', '['],
    '}:}': [']', ']'],
    'word:and': ['and', ''],
    'word:or': ['or', ''],
    'word:not': ['not', '!'],
    'word:': ['word', ''],
    'word:': ['word', '']
};
/**
 * DNCL
 */
export function convertDNCL2(tokens) {
    if (!useDNCL2mode(tokens)) {
        return tokens;
    }
    // 
    const lines = splitTokens(tokens, 'eol');
    for (let i = 0; i < lines.length; i++) {
        const line = lines[i];
        if (line.length <= 1) {
            continue;
        } // 
        // ---  ---
        // ()  ()
        const nai = findTokens(line, ['word:']);
        if (nai >= 1) {
            const tt = line[nai];
            if (tt.josi === '') {
                line[nai - 1].josi = '';
                line.splice(nai, 1);
            }
        }
        // (|)  
        for (let ni = 0; ni < line.length; ni++) {
            const t = line[ni];
            if (t.value === '' && t.josi === '') {
                t.type = '';
                t.value = '';
                t.josi = '';
            }
        }
        // ',': '',
        for (;;) {
            const ni = findTokens(line, ['word:', 'comma:,', 'word:']);
            if (ni < 0) {
                break;
            }
            const sou = line[ni + 2];
            if (sou.josi === '') {
                sou.type = '';
                sou.value = '';
                sou.josi = '';
                line.splice(ni, 3, sou);
                continue;
            }
            else if (sou.josi === '') {
                const nakumosi = line[ni + 3];
                if (nakumosi.value.substring(0, 4) === '') {
                    sou.type = '';
                    sou.value = '';
                    sou.josi = '';
                    line.splice(ni, 3, sou);
                    if (nakumosi.value.length > 4) {
                        const nakumosiTudukiStr = nakumosi.value.substring(4);
                        const nakumosiToken = NewEmptyToken('word', nakumosiTudukiStr, nakumosi.indent, nakumosi.line, nakumosi.file);
                        if (nakumosiTudukiStr.match(/^\d/)) {
                            nakumosiToken.type = 'number';
                        }
                        line.splice(ni + 2, 0, nakumosiToken);
                        nakumosi.value = nakumosi.value.substring(0, 4);
                    }
                    nakumosi.type = '';
                    nakumosi.value = '';
                    nakumosi.josi = '';
                    continue;
                }
            }
            break;
        }
        // I11001()|()|
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            const fu = line[ni];
            fu.type = 'word';
            fu.value = '';
            fu.josi = '';
            line.splice(ni, 2, fu);
        }
        // I11001()|()|
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            const fu = line[ni];
            fu.type = 'word';
            fu.value = '';
            fu.josi = '';
            line.splice(ni, 2, fu);
        }
        // I11001()|()|()
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            const fu = line[ni];
            fu.type = 'word';
            fu.value = '';
            fu.josi = '';
            line.splice(ni, 2, fu);
        }
        // I11001()|()|
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            const fu = line[ni];
            fu.type = 'word';
            fu.value = '';
            fu.josi = '';
            line.splice(ni, 2, fu);
        }
        // ---  ---
        for (let i = 0; i < line.length; i++) {
            // |Hindo||(|)|10|
            if (tokenEq([['word:', 'word:'], 'word', 'word:', ['word:', 'word:'], '*', 'word:'], line, i)) {
                const varToken = line[i + 1];
                varToken.josi = '';
                const valToken = line[i + 4];
                valToken.josi = '';
                line.splice(i, 6, varToken, newToken('eq', '=', varToken), newToken('word', ''), newToken('(', '('), newToken('[', '['), valToken, newToken(']', ']'), newToken('comma', ','), newToken('number', DNCL_ARRAY_INIT_COUNT), newToken(')', ')'));
                i += 6; // skip
            }
            // Hensu||(|)|0|
            if (tokenEq(['word', 'word:', ['word:', 'word:'], ['number', 'string', 'word'], 'word:'], line, i)) {
                const varToken = line[i];
                varToken.josi = '';
                const valToken = line[i + 3];
                valToken.josi = '';
                line.splice(i, 5, varToken, newToken('eq', '=', varToken), newToken('word', ''), newToken('(', '('), newToken('[', '['), valToken, newToken(']', ']'), newToken('comma', ','), newToken('number', DNCL_ARRAY_INIT_COUNT), newToken(')', ')'));
            }
            //  | xx | 
            if (tokenEq([['word:', 'word:'], 'word', 'word:'], line, i)) {
                const varToken = line[i + 1];
                varToken.josi = '';
                line.splice(i, 3, varToken, newToken('eq', '=', varToken), newToken('word', ''), newToken('(', '('), newToken('[', '['), newToken('number', 0), newToken(']', ']'), newToken('comma', ','), newToken('number', DNCL_ARRAY_INIT_COUNT), newToken(')', ')'));
            }
        }
        // ---  ---
        //  (255)  (255)
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            line[ni].value = '';
            line[ni].josi = '';
            line.splice(ni + 1, 1);
        }
        // ''  ''
        for (;;) {
            const ni = findTokens(line, ['word:', 'word:']);
            if (ni < 0) {
                break;
            }
            // 
            // 
            // DNCL
            // 
            const t = line[ni];
            t.value = '';
            t.josi = '';
            line.splice(ni + 1, 1);
        }
        // 
        let j = 0;
        while (j < line.length) {
            const t = line[j];
            // 
            if (t.type === 'word' && t.value.length >= 2) {
                const c = t.value.charAt(t.value.length - 1);
                if (c === '' || c === '') {
                    t.value = t.value.substring(0, t.value.length - 1);
                    t.josi = '';
                    line.splice(j + 1, 0, NewEmptyToken('word', c, t.indent, t.line, t.file));
                }
                j++;
                continue;
            }
            j++;
        }
    }
    // 
    for (let i = 0; i < tokens.length; i++) {
        const t = tokens[i];
        const a = DNCL_SIMPLES[t.type + ':' + t.value];
        if (a !== undefined) {
            t.type = a[0];
            t.value = a[1];
        }
    }
    tokens = joinTokenLines(lines);
    if (IS_DEBUG) {
        console.log('@@@---DNCL:tokens---');
        console.log(debugTokens(tokens));
        console.log('@@@/---DNCL:tokens---');
    }
    return tokens;
}
/**
 * 
 * @param typeValues ['word:']/'*'/":"/
 * @param lines 
 * @param fromIndex 
 * @returns 
 */
function tokenEq(typeValues, lines, fromIndex) {
    const check = (pattern, t) => {
        if (pattern instanceof Array) {
            for (let i = 0; i < pattern.length; i++) {
                if (check(pattern[i], t)) {
                    return true;
                }
            }
            return false;
        }
        if (pattern === '*') {
            return true;
        }
        if (pattern.indexOf(':') < 0) {
            if (pattern === t.type) {
                return true;
            }
            else {
                return false;
            }
        }
        const tv = `${t.type}:${t.value}`;
        if (pattern === tv) {
            return true;
        }
        return false;
    };
    for (let i = 0; i < typeValues.length; i++) {
        const idx = i + fromIndex;
        if (idx >= lines.length) {
            return false;
        }
        const pat = typeValues[i];
        const t = lines[idx];
        if (!check(pat, t)) {
            return false;
        }
    }
    return true;
}
function findTokens(tokens, findTypeValue) {
    const findA = findTypeValue.map(s => s.split(':'));
    for (let i = 0; i < tokens.length; i++) {
        let flag = true;
        for (let j = 0; j < findA.length; j++) {
            const f = findA[j];
            const idx = i + j;
            if (idx >= tokens.length) {
                return -1;
            }
            if (tokens[idx].type === f[0] && tokens[idx].value === f[1]) {
                continue;
            }
            else {
                flag = false;
                break;
            }
        }
        if (flag) {
            return i;
        }
    }
    return -1;
}
function useDNCL2mode(tokens) {
    // 100
    for (let i = 0; i < tokens.length; i++) {
        if (i > 100) {
            break;
        }
        const t = tokens[i];
        if (t.type === 'line_comment' && DNCL2_KEYWORDS.indexOf(t.value) >= 0) {
            t.type = 'DNCL2';
            return true;
        }
    }
    return false;
}
