/** NakoLogger */
import { NakoError, NakoRuntimeError } from './nako_errors.mjs';
import { NakoColors } from './nako_colors.mjs';
/**  -  */
export class LogLevel {
    // string to level no
    static fromS(levelStr) {
        let level = LogLevel.trace;
        switch (levelStr) {
            case 'all':
                level = LogLevel.all;
                break;
            case 'trace':
                level = LogLevel.trace;
                break;
            case 'debug':
                level = LogLevel.debug;
                break;
            case 'info':
                level = LogLevel.info;
                break;
            case 'warn':
                level = LogLevel.warn;
                break;
            case 'error':
                level = LogLevel.error;
                break;
            case 'stdout':
                level = LogLevel.stdout;
                break;
            default:
                throw new Error('[NakoLogger] unknown logger level:' + levelStr);
        }
        return level;
    }
    static toString(level) {
        const levels = ['all', 'trace', 'debug', 'info', 'warn', 'error', 'stdout'];
        return levels[level];
    }
}
// level no
LogLevel.all = 0;
LogLevel.trace = 1;
LogLevel.debug = 2;
LogLevel.info = 3;
LogLevel.warn = 4;
LogLevel.error = 5;
LogLevel.stdout = 6;
/**
 * 
 *  `stringifyPosition({ file: "foo.txt", line: 5 })`  `"foo.txt(6):"` 
 */
function stringifyPosition(p) {
    if (!p) {
        return '';
    }
    return `${p.file || ''}${p.line === undefined ? '' : `(${p.line + 1}): `}`;
}
/**
 * 
 * trace(), debug(), info(), warn(), error()  `[]` 
 * error() 
 */
export class NakoLogger {
    constructor() {
        this.listeners = [];
        this.logs = '';
        this.position = '';
    }
    getErrorLogs() {
        return [this.logs.replace(/\s+$/, ''), this.position];
    }
    clear() {
        this.logs = '';
        this.position = '';
    }
    /**
     * send
     * @param levelStr
     * @param callback
     */
    addListener(levelStr, callback) {
        const level = LogLevel.fromS(levelStr);
        this.listeners.push({ level, callback });
    }
    /**
     * addListener
     * @param {LogListener} callback
     */
    removeListener(callback) {
        this.listeners = this.listeners.filter((l) => l.callback !== callback);
    }
    /** debug
     * @param {string} message
     * @param {Position | null} position
     */
    trace(message, position = null) {
        this.sendI(LogLevel.trace, `${NakoColors.color.bold}[]${NakoColors.color.reset}${stringifyPosition(position)}${message}`, position);
    }
    /** 
     * @param {string} message
     * @param {Position | null} position
     */
    debug(message, position = null) {
        this.sendI(LogLevel.debug, `${NakoColors.color.bold}[]${NakoColors.color.reset}${stringifyPosition(position)}${message}`, position);
    }
    /** 
     * @param {string} message
     * @param {Position | null} position
     */
    info(message, position = null) {
        this.sendI(LogLevel.info, `${NakoColors.color.bold}${NakoColors.color.blue}[]${NakoColors.color.reset}${stringifyPosition(position)}${message}`, position);
    }
    /** 
     * @param {string} message
     * @param {Position | null} position
     */
    warn(message, position = null) {
        this.sendI(LogLevel.warn, `${NakoColors.color.bold}${NakoColors.color.green}[]${NakoColors.color.reset}${stringifyPosition(position)}${message}`, position);
    }
    /** 
     * @param {string | Error} message
     * @param {Position | null} position
     */
    error(message, position = null) {
        // NakoError (`message instanceof NakoError`)
        if (message instanceof Error && typeof message.type === 'string') {
            // NakoError
            const etype = message.type;
            switch (etype) {
                case 'NakoRuntimeError':
                case 'NakoError':
                    if (message instanceof NakoError) {
                        const e = message;
                        let pos = position;
                        if (pos === null || pos === undefined) {
                            pos = { file: e.file, line: e.line || 0, startOffset: 0, endOffset: 0 };
                        }
                        this.sendI(LogLevel.error, e.message, pos);
                        return;
                    }
            }
        }
        if (message instanceof Error) {
            // message
            message = message.message;
        }
        this.sendI(LogLevel.error, `${NakoColors.color.bold}${NakoColors.color.red}[]${NakoColors.color.reset}${stringifyPosition(position)}${message}`, position);
    }
    /** RuntimeError */
    runtimeError(error, posStr) {
        const e = new NakoRuntimeError(error, posStr);
        return e;
    }
    /** )
     * @param {string} message
     * @param {Position | null} position
     */
    stdout(message, position = null) {
        this.sendI(LogLevel.stdout, `${message}`, position);
    }
    /** levellistenerhtmlbrowserConsolenodeConsole */
    send(levelStr, nodeConsole, position, html = null, browserConsole = null) {
        const i = LogLevel.fromS(levelStr);
        this.sendI(i, nodeConsole, position, html, browserConsole);
    }
    /** levellistenerhtmlbrowserConsolenodeConsole */
    sendI(level, nodeConsole, position, html = null, browserConsole = null) {
        const makeData = () => {
            // nodeConsolenoColor, nodeCondole
            const formats = NakoColors.convertColorTextFormat(nodeConsole);
            // htmlborder
            let style = '';
            if (nodeConsole.includes('\n')) {
                style += 'border-top: 1px solid #8080806b; border-bottom: 1px solid #8080806b;';
            }
            // 
            const data = {
                noColor: formats.noColor,
                nodeConsole: formats.nodeConsole,
                browserConsole: browserConsole || formats.browserConsole,
                html: `<div style="${style}">` + (html || formats.html) + '</div>',
                level: LogLevel.toString(level),
                position
            };
            return data;
        };
        // 
        if (level === LogLevel.error) {
            const data = makeData();
            this.logs += data.noColor + '\n';
            if (position && this.position !== null) {
                this.position = `l${position.line}:${position.file}`;
            }
        }
        // 
        for (const l of this.listeners) {
            if (l.level <= level) {
                const data = makeData();
                l.callback(data);
            }
        }
    }
}
