/**
 * 3
 */
import { josiRE, removeJosiMap } from './nako_josi_list.mjs';
const kanakanji = /^[\u3005\u4E00-\u9FCF_a-zA-Z0-9-\u2460-\u24FF\u2776-\u277F\u3251-\u32BF]+/;
const hira = /^[-]/;
const allHiragana = /^[-]+$/;
const wordHasIjoIka = /^.+(|||)$/;
const wordSpecial = /^(|)/;
const errorRead = (ch) => {
    return function () { throw new Error('' + ch + ''); };
};
export const unitRE = /^(||||||||||||||||||||||||||m|mm|cm|km|g|kg|t|px|dot|pt|em|b|mb|kb|gb)/;
/**  */
export const rules = [
    // 
    { name: '', pattern: /^;;;/ },
    { name: 'eol', pattern: /^\n/ },
    { name: 'eol', pattern: /^;/ },
    // eslint-disable-next-line no-control-regex
    { name: 'space', pattern: /^(\x20|\x09|| ||)+/ },
    { name: 'comma', pattern: /^,/ },
    { name: 'line_comment', pattern: /^#[^\n]*/ },
    { name: 'line_comment', pattern: /^\/\/[^\n]*/ },
    { name: 'range_comment', pattern: /^\/\*/, cbParser: cbRangeComment },
    { name: 'def_test', pattern: /^:/ },
    { name: 'def_func', pattern: /^/ },
    //  --- nako_lexer(#994)
    { name: 'number', pattern: /^0[xX][0-9a-fA-F]+(_[0-9a-fA-F]+)*/, readJosi: true, cb: parseNumber },
    { name: 'number', pattern: /^0[oO][0-7]+(_[0-7]+)*/, readJosi: true, cb: parseNumber },
    { name: 'number', pattern: /^0[bB][0-1]+(_[0-1]+)*/, readJosi: true, cb: parseNumber },
    // 
    { name: 'number', pattern: /^\d+(_\d+)*\.(\d+(_\d+)*)?([eE][+|-]?\d+(_\d+)*)?/, readJosi: true, cb: parseNumber },
    { name: 'number', pattern: /^\.\d+(_\d+)*([eE][+|-]?\d+(_\d+)*)?/, readJosi: true, cb: parseNumber },
    { name: 'number', pattern: /^\d+(_\d+)*([eE][+|-]?\d+(_\d+)*)?/, readJosi: true, cb: parseNumber },
    { name: '', pattern: /^(),?/ },
    { name: '', pattern: /^(|)/ },
    { name: '', pattern: /^?/ },
    // 
    { name: '', pattern: /^()?/ },
    //  NakoLexer._replaceWord  word 
    // @see nako_reserved_words.js
    { name: 'shift_r0', pattern: /^>>>/ },
    { name: 'shift_r', pattern: /^>>/ },
    { name: 'shift_l', pattern: /^<</ },
    { name: '===', pattern: /^===/ },
    { name: '!==', pattern: /^!==/ },
    { name: 'gteq', pattern: /^(|>=|=>)/ },
    { name: 'lteq', pattern: /^(|<=|=<)/ },
    { name: 'noteq', pattern: /^(|<>|!=)/ },
    { name: '', pattern: /^(|<--)/ },
    { name: 'eq', pattern: /^(==|)/ },
    { name: 'eq', pattern: /^(=|)/ },
    { name: 'line_comment', pattern: /^(!|)(||DNCL|DNCL2|DNCL2)[^\n]*/ },
    { name: 'not', pattern: /^(!|)/ },
    { name: 'gt', pattern: /^>/ },
    { name: 'lt', pattern: /^</ },
    { name: 'and', pattern: /^(|&&)/ },
    { name: 'or', pattern: /^(|||\|\|)/ },
    { name: '@', pattern: /^@/ },
    { name: '+', pattern: /^\+/ },
    { name: '-', pattern: /^-/ },
    { name: '**', pattern: /^(|\*\*)/ },
    { name: '*', pattern: /^(|\*)/ },
    { name: '', pattern: /^/ },
    { name: '', pattern: /^(|\/)/ },
    { name: '%', pattern: /^%/ },
    { name: '^', pattern: /^\^/ },
    { name: '&', pattern: /^&/ },
    { name: '[', pattern: /^\[/ },
    { name: ']', pattern: /^]/, readJosi: true },
    { name: '(', pattern: /^\(/ },
    { name: ')', pattern: /^\)/, readJosi: true },
    { name: '|', pattern: /^\|/ },
    { name: 'string', pattern: /^/, cbParser: src => cbString('', '', src) },
    { name: 'string_ex', pattern: /^/, cbParser: src => cbString('', '', src) },
    { name: 'string_ex', pattern: /^/, cbParser: src => cbString('', '', src) },
    { name: 'string', pattern: /^/, cbParser: src => cbString('', '', src) },
    { name: 'string_ex', pattern: /^/, cbParser: src => cbString('', '', src) },
    { name: 'string_ex', pattern: /^"/, cbParser: src => cbString('"', '"', src) },
    { name: 'string', pattern: /^'/, cbParser: src => cbString('\'', '\'', src) },
    { name: '', pattern: /^/, cbParser: errorRead('') },
    { name: '', pattern: /^/, cbParser: errorRead('') },
    { name: 'func', pattern: /^\{\},?/ },
    { name: '{', pattern: /^\{/ },
    { name: '}', pattern: /^\}/, readJosi: true },
    { name: ':', pattern: /^:/ },
    { name: '_eol', pattern: /^_\s*\n/ },
    { name: 'dec_lineno', pattern: /^/ },
    //  = ()*
    { name: 'word', pattern: /^[\uD800-\uDBFF][\uDC00-\uDFFF][_a-zA-Z0-9]*/, readJosi: true },
    { name: 'word', pattern: /^[\u1F60-\u1F6F][_a-zA-Z0-9]*/, readJosi: true },
    { name: 'word', pattern: /^.+?/, readJosi: true },
    // 
    {
        name: 'word',
        pattern: /^[_a-zA-Z\u3005\u4E00-\u9FCF--\u2460-\u24FF\u2776-\u277F\u3251-\u32BF]/,
        cbParser: cbWordParser
    }
];
export function trimOkurigana(s) {
    // ()
    if (!hira.test(s)) {
        return s.replace(/[-]+/g, '');
    }
    //  () 
    if (allHiragana.test(s)) {
        return s;
    }
    //  () 
    return s.replace(/[-]+$/g, '');
}
// Utility for Rule
function cbRangeComment(src) {
    let res = '';
    const josi = '';
    let numEOL = 0;
    src = src.substring(2); // skip /*
    const i = src.indexOf('*/');
    if (i < 0) { // not found
        res = src;
        src = '';
    }
    else {
        res = src.substring(0, i);
        src = src.substring(i + 2);
    }
    // 
    for (let i = 0; i < res.length; i++) {
        if (res.charAt(i) === '\n') {
            numEOL++;
        }
    }
    res = res.replace(/(^\s+|\s+$)/, ''); // trim
    return { src, res, josi, numEOL };
}
/**
 * @param {string} src
 */
function cbWordParser(src, isTrimOkurigana = true) {
    /*
      kanji    = [\u3005\u4E00-\u9FCF]
      hiragana = [-]
      katakana = [-]
      emoji    = [\u1F60-\u1F6F]
      uni_extra = [\uD800-\uDBFF] [\uDC00-\uDFFF]
      alphabet = [_a-zA-Z]
      numchars = [0-9]
    */
    let res = '';
    let josi = '';
    while (src !== '') {
        // 1
        if (res.length > 0) {
            //  (#1379 core#84)
            const jsw = wordSpecial.exec(src);
            if (jsw) {
                break;
            }
            // 
            const j = josiRE.exec(src);
            if (j) {
                josi = j[0].replace(/^\s+/, '');
                src = src.substring(j[0].length);
                // , #877
                if (src.charAt(0) === ',') {
                    src = src.substr(1);
                }
                break;
            }
        }
        // 
        const m = kanakanji.exec(src);
        if (m) {
            res += m[0];
            src = src.substring(m[0].length);
            continue;
        }
        // 
        const h = hira.test(src);
        if (h) {
            res += src.charAt(0);
            src = src.substring(1);
            continue;
        }
        break; // other chars
    }
    // ---  ---
    //  (#831)
    // src
    if (/[-]$/.test(res)) {
        src = res.charAt(res.length - 1) + src;
        res = res.slice(0, -1);
    }
    //  #918
    const ii = wordHasIjoIka.exec(res);
    if (ii) {
        src = ii[1] + josi + src;
        josi = '';
        res = res.substring(0, res.length - ii[1].length);
    }
    //  #936 #939 #974
    if (removeJosiMap[josi]) {
        josi = '';
    }
    // 
    //  --- 
    if (isTrimOkurigana) {
        res = trimOkurigana(res);
    }
    // 
    if (res === '' && josi !== '') {
        res = josi;
        josi = '';
    }
    return { src, res, josi, numEOL: 0 };
}
function cbString(beginTag, closeTag, src) {
    let res = '';
    let josi = '';
    let numEOL = 0;
    src = src.substr(beginTag.length); // skip beginTag
    const i = src.indexOf(closeTag);
    if (i < 0) { // not found
        res = src;
        src = '';
    }
    else {
        res = src.substr(0, i);
        src = src.substr(i + closeTag.length);
        // res  beginTag  #953
        if (res.indexOf(beginTag) >= 0) {
            if (beginTag === '') {
                throw new Error('');
            }
            else {
                throw new Error(`${beginTag}${beginTag}`);
            }
        }
    }
    // 
    const j = josiRE.exec(src);
    if (j) {
        josi = j[0].replace(/^\s+/, '');
        src = src.substr(j[0].length);
        //  #877
        if (src.charAt(0) === ',') {
            src = src.substr(1);
        }
    }
    //  #936 #939 #974
    if (removeJosiMap[josi]) {
        josi = '';
    }
    // 
    for (let i = 0; i < res.length; i++) {
        if (res.charAt(i) === '\n') {
            numEOL++;
        }
    }
    return { src, res, josi, numEOL };
}
function parseNumber(n) {
    return Number(n.replace(/_/g, ''));
}
